/**
 *   (   -)
 * @param {Array} array - ,   
 * @return {Array}  
 */
export const mixArray = (array) => {
  for (let i = array.length - 1; i > 0; i--) {
    let k = Math.floor(Math.random() * (i + 1));
    let swap = array[k];
    array[k] = array[i];
    array[i] = swap;
  }

  return array;
};

/**
 *         (a  b)   
 * @param {Object} a -  
 * @param {Object} b -  
 * @return {Object}   
 */
export const extendObject = (a, b) => Object.assign({}, a, b);
